@use 'base/variables' as v;
@use 'base/mixins' as m;


.barra {
    display: none;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__navegacion {
        @include m.tablet {
            display: flex;
            gap: 4rem;            
        }
    }
    &__link {
        color: v.$blanco;        
    }
    &__texto {
        font-size: 5rem;
        color: v.$primario;
        margin: 0;
    }

}